import React, { useContext, useEffect, useState } from 'react'
import { Box, Image, Heading, Text, ResponsiveContext, Table, TableHeader, TableCell, TableBody, TableRow, DropButton, Select, Button, Form } from 'grommet';
import Hero from '../../../Components/Hero/Hero';
import BackOfficeDashboardImage from "../../../assets/images/dark-dashboard.jpg";
import styled from 'styled-components';
import Axios from 'axios';
import { IBackOfficeClaims, IUserAccount, IUserBank, DateRange } from '../../../Types';
import BetPlatformData from "../../../_data/betPlatforms.json";
import BanksData from "../../../_data/banks.json";
import Popup from 'reactjs-popup';
import ProgressBar from '../../../Components/ProgressBar/ProgressBar';
import SnackBarComponent, { SnackBarProps } from '../../../Components/SnackBar/SnackBar';
import spinner from "../../../assets/svg/spinner.svg";
import Scrollbars from 'react-custom-scrollbars';
import AssertivePopup, { AssertivePopupProps } from '../../../Components/_Grommet/Popups/AssertivePopup';
import RangeCalendar from '../../../Components/_Grommet/Selects/RangeCalendar';
import PatientInput from '../../../Components/_Grommet/Inputs/PatientInput';
import SlimSpinner from '../../../Components/_Grommet/Spinners/SlimSpinner/SlimSpinner';
import EmptyImage from "../../../assets/illustrations/eastwood-no-messages.png";
import moment from 'moment';

const Wrapper = styled(Box)`
	width: 100vw;
	align-items: center;
	padding-bottom: 2rem;
	@media (min-width: 768px) {
		// align-items: start;
	}
`;

const Claims = () => {
    const size = useContext(ResponsiveContext);
    const [loading, setLoading] = useState(false);
    const [claims, setClaims] = useState([] as IBackOfficeClaims[]);
    
    //const [assertingChange, setAssertingChange] = useState(false);

    const [currentClaim, setCurrentClaim] = useState({} as IBackOfficeClaims);
    const [currentClaimStatus, setCurrentClaimStatus] = useState("");
    const [snackbar, setSnackbar] = useState({} as SnackBarProps);
    const [popupState, setPopupState] = useState({} as AssertivePopupProps);

    const [loadingClaim, setLoadingClaim] = useState(true);
    const [viewClaim, setViewClaim] = useState(false);
    const [currentClaimUser, setCurrentClaimUser] = useState({} as IUserAccount);

    const [dateRange, setDateRange] = useState({start: new Date(), end: new Date()} as DateRange);
	const [searchQuery, setSearchQuery] = useState("");
    const [filter, setFilter] = useState({ name: "All", filter: "" });

    const filters = [
        { name: "All", filter: "" },
        { name: "Regular", filter: "cashedOut==false"},
        { name: "Claimed", filter: "cashedOut==true" }
    ];

    let currentClaimUserId = "";    
    let eventHandled = false;

    useEffect(() => {
        loadClaims();
    }, [dateRange, searchQuery, filter]);

    const loadClaims = () => {
        setLoading(true);
        // TODO: Query results with search and date parameters
        Axios.get<IBackOfficeClaims[]>("/api/backOffice/Accounts/bets", { params: { start: dateRange.start, end: dateRange.end, searchQuery, filters: filter.filter } })
            .then((res) => {
                if (res.status === 200) {
                    console.log(res.data);
                    setClaims(res.data.sort((a, b) => {
                        var dateA = new Date(a.cashedOutOn), dateB = new Date(b.cashedOutOn);
    				    return dateA > dateB ? -1 : dateA < dateB ? 1 : 0;
                    }));
                }
            }).catch((err) => {
                // Display some error message
                console.log(err);
            }).finally(() => {
                setLoading(false);
            });
    }

    const getClaimUser = () => {
        setLoadingClaim(true);
        Axios.get<IUserAccount>("/api/backOffice/Accounts/single", { params: { id: currentClaimUserId } })
            .then((res) => {
                if (res.status === 200) {
                    setCurrentClaimUser(res.data);
                    console.log(res.data);
                }
            }).catch((err) => {
                console.log(err);
            }).finally(() => {
                setLoadingClaim(false);
            });
    }

    const updateClaim = ({claim, status}: {claim: IBackOfficeClaims, status: string}) => {
        if (!currentClaim) {
            return;
        }

        setLoading(true);
        Axios.post("/api/backOffice/Accounts/claims/update", { id: claim.id, status })
            .then((res) => {
                if (res.status) {
                    setSnackbar({
                        message: `Successfully changed status to ${status}`,
                        variant: "success",
                        show: true
                    })
                }
            }).catch((err) => {
                setSnackbar({
                    message: "An error occured while trying to update the claim, please try again.",
                    variant: "error",
                    show: true
                })
            }).finally(() => {
                loadClaims();
            });
    }


	return (
        <>
		<Wrapper>
            <SnackBarComponent
                message={snackbar.message}
                show={snackbar.show}
                variant={snackbar.variant}
                onClose={() => setSnackbar((s) => ({ ...s, show: false }))}
            />

			<Hero
				image={BackOfficeDashboardImage}
				text="Bets"
			/>
            
            <AssertivePopup 
                title={popupState.title} open={popupState.open} 
                message={popupState.message} action={popupState.action} size={size}
                onClose={() => {
                    setPopupState({open: false, message: "", action: () => {}})
                }}
            />


            <Popup position="center center" open={viewClaim} modal
                closeOnDocumentClick={true}
                onClose={() => setViewClaim(false)}
                contentStyle={{
                    width: size === "small" ? "60%" : "700px",
                    height: size === "small" ? "200px" : "400px",
                    borderRadius: "20px",
                    boxShadow: "6px 7px 13px -3px rgba(0,0,0,0.5)"
                }}
                >
                <Box width="100%" height="100%">
                    <Box pad={{
                            vertical: "10px",
                            horizontal: size === "small" ? "10px" : "20px"	
                        }} align="center"
                        width="100%"
                        height="100%"
                        justify="between"
                    >
                        {(loadingClaim && currentClaim) ? (
                            <Image src={spinner} width="100px" margin="auto"/>
                        ) : (
                            <Box width="100%" height="100%">
                                <Scrollbars autoHide style={{
                                    padding: "100px"
                                }}>
                                    <Box direction="column" justify="start">
                                        <Image
                                            width="120px"
                                            max-width="120px"
                                            fit="contain" 
                                            src={currentClaim.platform!.logo}
                                        />

                                        <Box direction="row" gap="medium">
                                            <Text>Slip Number: </Text>
                                            <Text>{currentClaim.slipNumber}</Text>
                                        </Box>

                                        <Box direction="row" gap="medium">
                                            <Text>Name: </Text>
                                            <Text>{currentClaimUser.firstName + " " + currentClaimUser.lastName}</Text>
                                        </Box>

                                        <Box direction="row" gap="medium">
                                            <Text>Phone Number: </Text>
                                            <Text>{currentClaimUser.phoneNumber}</Text>
                                        </Box>

                                        <Box>
                                            <Heading level={3}>
                                                Bank Accounts
                                            </Heading>

                                            {
                                                currentClaimUser.bankAccounts ? (
                                                    currentClaimUser.bankAccounts.map((account: IUserBank, index: number, array: IUserBank[]) => {
                                                        const bank = BanksData[account.bankId - 1];
                                                        return (
                                                        <Box height="40px" direction="row" gap="small" margin={{vertical: "5px"}}>
                                                            <Box width="150px">
                                                                <Text>
                                                                    {account.accountName}
                                                                </Text>
                                                            </Box>

                                                            <Box width="120px">
                                                                <Text>
                                                                    {account.accountNumber}
                                                                </Text>
                                                            </Box>
                                                           
                                                            <Box width="100px">
                                                                <Text>
                                                                    {bank.knownAs}
                                                                </Text>
                                                            </Box>

                                                            <Box align="center" justify="center" alignSelf="start">
                                                                <Image src={bank.logo} height="30px"/>
                                                            </Box>
                                                            
                                                        </Box>
                                                    )})
                                                ) : null
                                            }
                                            
                                        </Box>
                                    </Box>
                                </Scrollbars>
                            </Box>
                        )}
                    </Box>
                </Box>
            </Popup>



            <Box justify="center" align="center">
                <Box
                    pad="large"
                    width={size !== "small" ? "90vw" : "80vw"}
                    background="white"
                    overflow={{ horizontal: "auto" }}
                    align="center"
                    justify="center"
                    round="small"
                    elevation="medium"
                    margin={{top: "large"}}
                >
                    <Box width="100%">
						<Box width="100%">
							<Form>
								<Box width="100%" direction="row" margin={{vertical: "small"}} justify="between">
									<RangeCalendar onChange={(range: { start: Date, end: Date}, target: RangeCalendar) => {
										setDateRange({start: range.start, end: range.end});
									}} />

                                    <Box direction="row" gap="medium" pad={{top: "small"}} alignSelf="end">
                                        <Box direction="row" gap="2px" margin={{bottom: "small"}} pad={{top: "small"}} alignSelf="end">
                                            <Box width="32px" color="#9060EB" alignSelf="center">
                                                <i className="zwicon-filter" style={{color: "#9060EB"}} />
                                            </Box>

                                            <Select 
                                                value={filter.name}
                                                options={filters.map(f => f.name)}
                                                onChange={(option) => setFilter(filters[option.selected])}
                                            />
                                        </Box>

                                        <Box direction="row" gap="2px" pad={{top: "small"}}  alignSelf="end">
                                            <Box width="32px" color="#9060EB" alignSelf="center">
                                                <i className="zwicon-search" style={{color: "#9060EB"}} />
                                            </Box>

                                            <PatientInput waitInterval={3000} 
                                                placeholder="Search" value={searchQuery} onChange={(event) => {  setSearchQuery(event.target.value); }}/>
                                        </Box>
                                    </Box>
								</Box>
							</Form>
						</Box>
					</Box>

                    <Box width="100%" style={{position: "relative", minHeight: "500px"}} margin={{top: "30px"}}>
						{!loading && (!claims || claims.length <= 0) ? (
							<Box height="100%" width="100%" 
								align="center" justify="center"
								style={{
									position: "absolute",
									top: 0, left: 0,
								}}
							>
								<Box height="300px" width="auto">
									<Image src={EmptyImage} fit="contain"/>
								</Box>

								<Box width="500px" align="center" justify="center">
									<Heading margin={{top: "0px"}} level="4">Huh? What happened to the Postman?</Heading>
									<Text weight={400} size="16px" textAlign="center">
										There doesn't seem to be any data to display, try searching or filtering to find
										what you want.
									</Text>
								</Box>
							</Box>
						) : null}
						
						<SlimSpinner active={loading}/>

                        
                        <Table cellPadding="medium" className="hoverTable">
                            <TableHeader
                                style={{
                                    borderBottom: "solid 1px #ccc",
                                    fontSize: "14px !important",
                                }}
                            >
                                <TableRow>
                                    <TableCell scope="col">
                                        Created
                                    </TableCell>
                                    <TableCell scope="col">
                                        Date Claimed
                                    </TableCell>
                                    <TableCell scope="col">
                                        Bet Company
                                    </TableCell>
                                    <TableCell scope="col">
                                        Slip Number
                                    </TableCell>
                                    <TableCell scope="col">
                                        Odds
                                    </TableCell>
                                    <TableCell scope="col">
                                        <strong>
                                            Stake
                                        </strong>
                                    </TableCell>
                                    <TableCell>
                                        <strong>
                                            Potential Winnings
                                        </strong>
                                    </TableCell>
                                    <TableCell>
                                        Status
                                    </TableCell>
                                </TableRow>
                            </TableHeader>

                            <TableBody>
                                {claims.map((claim, index) => (
                                    <TableRow
                                        key={index}
                                        style={{
                                            padding: ".1rem",
                                        }}
                                        onClick={(event) => {
                                            if (eventHandled) {
                                                eventHandled = false;
                                                return;
                                            }
                                            
                                            currentClaimUserId = claim.userId;
                                            claim.platform = BetPlatformData[claim.platformId - 1];
                                            setCurrentClaim(claim);
                                            setViewClaim(true);
                                            getClaimUser();
                                            
                                        }}
                                    >
                                        <TableCell>
                                            {moment(claim.date).format("lll")}
                                        </TableCell>
                                        <TableCell>
                                            {!claim.cashedOutOn || moment(claim.cashedOutOn).year() == 1 ?  "-" : moment(claim.cashedOutOn).format("lll")}
                                        </TableCell>
                                        <TableCell>
                                            {(claim.platformId > 0 && claim.platformId < BetPlatformData.length) ? BetPlatformData.find((f) => f.id === claim.platformId)!.name : ""}
                                        </TableCell>
                                        <TableCell>
                                            {claim.slipNumber}
                                        </TableCell>
                                        <TableCell>
                                            {claim.odds}
                                        </TableCell>
                                        <TableCell>
                                            <strong>
                                                {"₦ " + claim.stake.toLocaleString()}
                                            </strong>
                                        </TableCell>
                                        <TableCell>
                                            <strong>
                                                {"₦ " + claim.potentialWinnings.toLocaleString()}
                                            </strong>
                                        </TableCell>
                                        <TableCell>
                                            {/* TODO: Use an enum */}
                                            <Box pad="0px"
                                                onClick={() => {
                                                    eventHandled = true;
                                                }}
                                            >
                                                <Select
                                                    disabled={!claim.cashedOut}
                                                    value={claim.status}
                                                    valueLabel={(
                                                        <Box
                                                            round="small"
                                                            overflow="hidden"
                                                            align="center"
                                                        >
                                                            {claim.status}
                                                        </Box>
                                                    )}
                                                    icon={true}
                                                    options={["Approved", "Declined", "Pending"]}
                                                    onChange={({ option }) => {
                                                        
                                                        claim.platform = BetPlatformData[claim.platformId - 1];
                                                        
                                                        setPopupState({
                                                            open: true,
                                                            title: `Set Status to ${claim.status}`,
                                                            message: `Claim ${claim.slipNumber} will be set to updated ${option}.`
                                                                + ` Are you sure you would like to perform this action?`, action: () => updateClaim({claim, status: option})})
                                                    }}
                                                    plain
                                                />
                                            </Box>
                                            
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>   
				    </Box>
   
                </Box>
            </Box>
			
		</Wrapper>
        </>
	)
}

export default Claims;
